import React, { Component } from 'react';
import { render } from 'react-dom';
import { Router, Route, IndexRoute, hashHistory } from 'react-router';

/*  */
import TopBar from './components/common/topBar'; // 
import HomePage from './components/home_page'; // 
import DetailPage from './components/detail/detail_page'; // 
import OrderDetail from './components/detail/order_detail'; // 
import ClearingDetail from './components/detail/clearing_detail'; // 
import ClearingPage from './components/clearing/clearing_page'; // 

// 
require('normalize.css');
require('./sass/main.scss');

// font-awesome 
require('font-awesome-webpack');

/**
 * 
 */
class App extends Component {
	render() {
		return (
			<div>
				<TopBar />
				{ this.props.children }
			</div>
		);
	}
}

render(
	<Router history={ hashHistory }>
		<Route path='/' component={ App }>
			<IndexRoute component={ HomePage }/>
			<Route path='/detail' component={ DetailPage }>
				<IndexRoute component={ OrderDetail }/>
				<Route path='/detail/clearing' component={ ClearingDetail }/>
			</Route>
			<Route path='/clearing' component={ ClearingPage }/> 
		</Route>
	</Router>,
	document.getElementById('main')
)


