/**
 * 
 */

import React, { Component } from 'react';
import { Link, IndexLink } from 'react-router';
import LoginAndReg from './loginAndreg';
import UserHandle from './userHandle';

export default class TopBar extends Component {
	constructor(props) {
		super(props);
		this.state = {
			isLogin: true
		}
	}

	/*
	 * 
	 */
	login() {
		this.setState({
			showLogin: false,
			isLogin: true
		});
	}

	render() {
		return (
			<div className='top-bar-box'>
				<div className='top-bar'>
					<IndexLink to="/"><i className="logo"></i></IndexLink>
					<ul className="bar-list clearfix">
						<li>
							<IndexLink className="bar-btn" activeClassName="active" to="/"></IndexLink>
						</li>
						<li>
							<Link className="bar-btn" activeClassName="active" to="/detail"></Link>
						</li>
						<li>
							<Link className="bar-btn" activeClassName="active" to="/clearing"></Link>
						</li>
					</ul>
					{ this.state.isLogin ? <UserBar /> : <LoginBar /> }
				</div>
			</div>
		);
	}
};

/** 
 * 
 */
class LoginBar extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showLogin: true,
			isLogin: true
		}
	}

	/*
	 * 
	 */
	showLoginModal(type) {
		this.setState({
			showLogin: true,
			isLogin: type
		});
	}

	/*
	 * 
	 */
	hideLoginModal() {
		this.setState({
			showLogin: false,
			isLogin: true
		});
	}

	render() {
		return (
			<div>
				<div className="account-handle">
					<a href="javascript:;" onClick={ this.showLoginModal.bind(this, true) }></a>
					<span>|</span>
					<a href="javascript:;" onClick={ this.showLoginModal.bind(this, false) }></a>
				</div>
				<LoginAndReg 
					showLogin={ this.state.showLogin } 
					isLogin={ this.state.isLogin } 
					onClose={ this.hideLoginModal.bind(this) } 
				/>
			</div>
		);
	}
}

/**
 * 
 */
class UserBar extends Component {
	constructor(props) {
		super(props);
		this.state = {
			photo: '/img/photo.jpeg',
			showHandle: true
		}
	}

	render() {
		return (
			<div className="user-bar">
				<img className="photo" src={ this.state.photo } alt=""/>
				<span className="user-email">shi_zhaojun@aliyun.com <i className="fa fa-angle-down user-down"></i></span>
				{ this.state.showHandle ? <UserHandle /> : null }
			</div>
		);
	}
}


